import { Alert, Grid, TextField } from '@mui/material'
import { useFormik } from 'formik'
import * as yup from 'yup'
import theme from '../../defaultTheme'
import { useAppDispatch, useAppSelector } from '../../store/hooks'
import { register } from '../../store/reducers/authorization'
import styles from './form.module.scss'

const validationSchem = yup.object({
  name: yup
    .string()
    .min(3, '    3 ') // Name should be of minimum 3 characters length
    .required('   '), // Name is required
  email: yup
    .string()
    .email('  e-mail')
    .required('Email   '), // Enter a valid email

  password: yup
    .string()
    .matches(/[0-9]/, '     ') // Password must contain at least one number
    .matches(/[A-Z-]/, '       ') // Password must contain at least one uppercase letter
    .min(8, '   8 ') // Password should be of minimum 8 characters length
    .matches(
      /^(?=.*[a-z-])(?=.*[A-Z-])(?=.*\d)(?=.*[^\da-zA-Z--\s])(?!.*\s).{8,}$/,
      '       ',
    ) // Password must contain at least one special symbol
    .required('   '), // Password is required
  passwordConfirm: yup
    .string()
    .oneOf([yup.ref('password'), null], '  ') // Password must much
    .required('  '), // Confirm password is required
})
interface ViewProps {
  formId: string
}

export const RegistrationView = ({ formId }: ViewProps) => {
  const dispatch = useAppDispatch()
  const error = useAppSelector(state => state.authorization.serverError);
  const formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      password: '',
      passwordConfirm: '',
    },
    validationSchema: validationSchem,
    onSubmit: (values) => {
      console.log({ name: values.name, email: values.email, password: values.password })
      dispatch(register({
        name: values.name,
        email: values.email,
        password: values.password
      }))
    },
  })
  return (
    <form id={formId} onSubmit={formik.handleSubmit} className={styles.form__content}>
      <Grid container spacing={2}>
        {error !== null && (
          <Grid item xs={12}>
            <Alert color='error' severity="error">{error}</Alert>
          </Grid>
        )}
        <Grid item xs={12}>
          <TextField
            id='name'
            name='name'
            label=''
            fullWidth
            value={formik.values.name}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.name && Boolean(formik.errors.name)}
            helperText={formik.errors.name}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            id='email'
            name='email'
            label='Email'
            fullWidth
            value={formik.values.email}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.email && Boolean(formik.errors.email)}
            helperText={formik.errors.email}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            id='password'
            name='password'
            label=''
            fullWidth
            value={formik.values.password}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.password && Boolean(formik.errors.password)}
            type='password'
            helperText={formik.errors.password}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            id='passwordConfirm'
            name='passwordConfirm'
            label=' '
            fullWidth
            value={formik.values.passwordConfirm}
            onChange={formik.handleChange}
            onBlur={formik.handleBlur}
            error={formik.touched.passwordConfirm && Boolean(formik.errors.passwordConfirm)}
            type='password'
            helperText={formik.errors.passwordConfirm}
          />
        </Grid>
      </Grid>
    </form>
  )
}
